import React, {Component} from 'react';
import './style.scss'
import ajax from '@/api/axios'


class PopularActivities extends Component {
  constructor(props) {
    super(props);
    this.state = ({
      //
      Hotdata: [],
      flag: false,
      //
      index: 0,
      //
      shopArr: [],
      //
      realData: []
    })
  }
  //
  componentDidMount() {
    //axios
    ajax.get('/v1/hot_activity').then(data => {
      //
      this.setState({
        //Hotdata
        Hotdata: data.data,
        //realData
        realData: data.data
        //
      }, () => {
        //flag
        this.setState({
          flag: true
        })
      });
      // console.log(data.data);
    })
  }

  //value
  change(value) {
    //
    this.setState({
      //index-----valueindex
      index: value,
      realData: []
    });
    //value0
    switch (value) {
      case 0 :
        this.setState({
          realData: this.state.Hotdata
        })
        break;
      case 1 :
        if (this.state.shopArr.length > 0) {
          this.setState({
            realData: this.state.shopArr
          })
        } else {
          ajax.get("/v1/preferential").then(data => {
            console.log(data.data);
            this.setState({
              shopArr: data.data,
              realData: data.data
            })
          })
        }
        break;

      case 2:
        let arr = []
        this.state.Hotdata.map(item => {
          if (item.iStatus == 999) {
            arr.push(item)
          }
        })
        this.setState({
          realData: arr
        })
        break
    }
  }

  render() {
    let arr = [];
    if (this.state.flag) {
      this.state.realData.slice(0, 4).map((item, index) => {
        arr.push(
          <li key={index}>
            <div className="a-1"><img src={item.sSmallnewImgUrl}/></div>
            <div className="a-2">
              <p>{item.sName}</p>
              <a>{item.dtEnd}</a>
            </div>
          </li>
        )
      })
    }
    return (
      <div className="popular">
        <div className="margin_pac">
          <div className="popular-left">
            <div className="popular-left-top">
              <h3></h3>
              <div className="popular-left-title">
                <span className={`a-1 ${this.state.index === 0 ? "active1" : ""}`}
                      onClick={this.change.bind(this, 0)}></span>
                <span className={`a-1 ${this.state.index === 1 ? "active1" : ""}`}
                      onClick={this.change.bind(this, 1)}></span>
                <span className={`a-1 ${this.state.index === 2 ? "active1" : ""}`}
                      onClick={this.change.bind(this, 2)}></span>
                <span className="te"></span>
              </div>
            </div>
            <div className="popular-left-bottom">
              <ul>

                {arr}

              </ul>
            </div>
          </div>
          <div className="popular-right">
            <div className="popular-right-top">
              <div className="left">
                <video className="side-down-video" autoPlay loop muted width="366" height="169">
                  <source src="//ossweb-img.qq.com/images/lol/v3/btn-download.mp4"/>
                </video>

              </div>
              <div className="right">
                <p></p>
                <p></p>
              </div>
            </div>
            <div className="popular-right-bottom">
              <ul>
                <li><a className="a-1"></a></li>
                <li><a className="a-2"></a></li>
                <li><a className="a-3"></a></li>
                <li><a className="a-4"></a></li>
              </ul>
              <div className="threebox">
                <p className="b-1"><a className="b-4"></a><a className="b-5"></a></p>
                <p className="b-2"><a className="b-6"></a>LOL</p>
                <p className="b-3"><a className="b-7"></a></p>
              </div>
            </div>
          </div>
        </div>
      </div>
    )
  }
}


export default PopularActivities